.container {
    border: 1px solid blue;
    padding: 20px;
    margin: 10px;

}
.UserCard {
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
    margin: 10px;
}
.UserCard img {
    width: 200px;
    aspect-ratio: 1;
    border-radius: 50%;
    border: 1px solid black;
}
.UserCard section div {
    display: flex;
}
.UserCard section div:first-child button{
    width: 20%;
    height: 20px;
    margin: 23% 0 0 40%;
}
.UserCard section div:first-child section {
    margin-left: 20px;
}
.UserCard section div:first-child section h2, .UserCard section div:first-child section p {
    width: 150%;
    text-decoration: 1px solid black underline;
}

.UserCard section div:last-child div{
    display: block;
}
.UserCard section div:last-child div p {
    margin-left: 22px;
    margin-top: -10px;
}
.UserCard section div:last-child div h3 {
    color: black;
}
.UserCard section div:first-child button:hover {
    background-color: white;
    font-weight: 600;
    border: none;
}